.FinishedQuiz {
    width: 600px;
    padding: 30px;
    border: 2px solid #fff;
    border-radius: 10px;
}

.FinishedQuiz ul {
    list-style: none;
    margin: 0;
    padding: 0;
    margin-bottom: 20px;
}

.FinishedQuiz ul li {
    margin-bottom: 7px;
}

.FinishedQuiz ul li i {
    margin-left: 10px;
}

.success i {
    color: rgb(13, 190, 81);
}

.error i {
    color: rgb(190, 13, 13);
}